import React, { useEffect, useState } from 'react'
import {TypesContainer,FieldActions,Label, ActionBtn} from '../../Styles'
import { AiOutlineEdit,AiOutlineDelete,AiOutlineCopy } from "react-icons/ai";
import EditFormValue from './EditFormValue';
import Select from 'react-select'
import StateContext from '../../../context/StateContext';
import Toggle from 'react-toggle'
import "react-toggle/style.css"

const RadioGroup = ({data,toggle}) => {
    return (

      <>
       <input type="radio" value={data.value} defaultChecked={data.selected} name={data.value} /> {data.value.toUpperCase()}
      </>
    )
}
const Radio  =({type,name,index})=> {
    const [data,setData] = useState(null)
    const [selectData,setSelectData] = useState(null)
    useEffect(()=>{
        setSelectData(type.values)
    },[])
    useEffect(()=>{
        setSelectData(type.values)
    },[type])

    const [editIsOpen,setEditOpen] = useState(false)
    useEffect(()=>{
      setData(type)
    },[type])
    useEffect(()=>{
      setData(type)
    },[]) 
    const onChangeValue=(event) =>{
    console.log(event.target.value);
  }
    return (
        <StateContext.Consumer>
          {({handleRemoveClick,onChangeHandler,handleCopyClick})=>{
            return(<TypesContainer>
                <FieldActions >
                <ActionBtn onClick={()=>handleRemoveClick(index)} hoverBg={'#c66865'} del={true}><AiOutlineDelete/></ActionBtn>
                <ActionBtn hoverBg={'#65aac6'} onClick={()=>setEditOpen(!editIsOpen)}><AiOutlineEdit/></ActionBtn>
                <ActionBtn hoverBg={'#6fc665'} onClick={()=>handleCopyClick(index,data)}><AiOutlineCopy/></ActionBtn>
                </FieldActions>
                {editIsOpen && <Label htmlFor={name} >{data.label}</Label>}
                
                {!editIsOpen && data &&
                <>
                <Label htmlFor={name} >{data.label}</Label>
                <div onChange={onChangeValue}>
                  {selectData.map((v,k)=>{
                    return(
                    <div style={{display: data.inline ? 'inline-block' : 'block'}}>
                      <RadioGroup data={v} key={k} toggle={data.toggle}/>
                    </div> )
                  })}
                </div>
                </>
                }
                
                <div style={{display:'block'}}>
                  {editIsOpen && <EditFormValue selectData={selectData} setSelectData={setSelectData} setData={setData} onChangeHandeler={onChangeHandler} index={index} data={data} name={name}/>}
                </div>
                </TypesContainer>)
          }}
            </StateContext.Consumer>
          )

}

export default  Radio
