import React from 'react'
import { useForm, } from "react-hook-form";
import {FormElemnts,FormGroup,InputWrap,EditLabel} from '../../Styles'
import Select from 'react-select'
const customStyles = {
  container: (provided, state) => ({
    ...provided,
    width: '100%',
  }),
}
const typess =['textarea','quill','tinymice']
const types = typess.map(v=>{
  return {
    value:v,
    label:v.toUpperCase()
  }
})
function EditFormValue({name,index,data,setData,onChangeHandeler}) {
    const {register,getValues, setValue} = useForm({mode: 'onChange',  defaultValues:data})
    // useEffect(()=>{
    //    let value = watch()
    //    console.log(value)
    //    if(value.subtype){
    //        let nV = {...value,subtype:value.subtype.value}
    //        setData(nV)
    //    }else{
    //        setData(value)
    //    }
    // },[])
    const handleChange=(data)=>{
        let value = getValues()
        console.log(value)
        setValue('subtype',data.value)
        let nV = {...value,subtype:data.value}
        console.log(nV)
        setData(nV)
        onChangeHandeler(nV,index)
    }
    const onChange=()=>{
       let value = getValues()
       console.log(value)
       if(value.subtype && value.subtype.value){
           let nV = {...value,subtype:value.subtype.value}
           setData(nV)
        onChangeHandeler(nV,index)

       }else{
           setData(value)
        onChangeHandeler(value,index)

       }
    }
    return (
        <FormElemnts>
        <form onChange={onChange}>
            <FormGroup>
                <EditLabel htmlFor={name}>Label</EditLabel>
                <InputWrap>
                  <textarea name={name} className={'inputItems'} style={{minHeight: '150px'}} {...register('label', { required: true })}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'description'}>Help Text</EditLabel>
                <InputWrap>
                  <input type='text' name={'description'} className={'inputItems'} {...register('description')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'placeholder'}>placeholder</EditLabel>
                <InputWrap>
                  <input type='text' name={'placeholder'} className={'inputItems'} {...register('placeholder')}/>
                </InputWrap>
            </FormGroup>
            
            <FormGroup>
                <EditLabel htmlFor={'className'}>class</EditLabel>
                <InputWrap>
                  <input type='text' name={'className'} placeholder="use space for separeted class name" className={'inputItems'} {...register('className')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'name'}>Name</EditLabel>
                <InputWrap>
                  <input type='text' name={'name'} className={'inputItems'} {...register('name')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
              <EditLabel>Types</EditLabel>
              <InputWrap>
               <Select 
                className="basic-single"
                classNamePrefix="select"
                defaultValue={{value:data.subtype,label:data.subtype.toUpperCase()}}
                isSearchable={false}
                name="color"
                options={types}
                styles={customStyles}
                onChange={handleChange}
                />
              
                </InputWrap> 
              
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'value'}>value</EditLabel>
                <InputWrap>
                  <input type='text' name={'value'} placeholder='Value' className={'inputItems'} {...register('value')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'maxlength'}>max length</EditLabel>
                  <InputWrap>
                  <input type='number' name={'maxlength'} className={'inputItems'} style={{width:'40%'}} {...register('maxlength')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'rows'}>Rows</EditLabel>
                  <InputWrap>
                  <input type='number' name={'rows'} className={'inputItems'} style={{width:'40%'}} {...register('rows')}/>
                </InputWrap>
            </FormGroup>
            
            </form>
        </FormElemnts>
    )
}

export default EditFormValue
