import React from 'react'
import { useForm, } from "react-hook-form";
import {FormElemnts,FormGroup,InputWrap,EditLabel} from '../../Styles'
import Select from 'react-select'
const customStyles = {
  container: (provided, state) => ({
    ...provided,
    width: '100%',
  }),
}
const btns =['default','danger','warning','info','primary','success']
const buttons = btns.map(v=>{
  return {
    value:v,
    label:v.toUpperCase()
  }
})
function EditFormValue({name,index,data,setData,onChangeHandeler}) {
    const {register,getValues, setValue} = useForm({mode: 'onChange',  defaultValues:data})
    // useEffect(()=>{
    //    let value = watch()
    //    console.log(value)
    //    if(value.subtype){
    //        let nV = {...value,subtype:value.subtype.value}
    //        setData(nV)
    //    }else{
    //        setData(value)
    //    }
    // },[])
    const handleChange=(data)=>{
        let value = getValues()
        setValue('subtype',data.value)
        let nV = {...value,subtype:data.value}
        console.log(nV)
        setData(nV)
        onChangeHandeler(nV,index)
    }
    const onChange=()=>{
       let value = getValues()
       console.log(value)
       if(value.subtype && value.subtype.value){
           let nV = {...value,subtype:value.subtype.value}
           setData(nV)
        onChangeHandeler(nV,index)

       }else{
           setData(value)
        onChangeHandeler(value,index)

       }
    }
    return (
        <FormElemnts>
        <form onChange={onChange}>
        <FormGroup>
                <EditLabel htmlFor={'required'}>Required</EditLabel>
                <InputWrap>
                  <input type='checkbox' name={'required'} {...register('required')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={name}>Label</EditLabel>
                <InputWrap>
                  <input type='text' name={name} className={'inputItems'} {...register('label', { required: true })}/>
                </InputWrap>
                    
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'description'}>Help Text</EditLabel>
                <InputWrap>
                  <input type='text' name={'description'} className={'inputItems'} {...register('description')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'placeholder'}>placeholder</EditLabel>
                <InputWrap>
                  <input type='text' name={'placeholder'} className={'inputItems'} {...register('placeholder')}/>
                </InputWrap>
            </FormGroup>
            
            <FormGroup>
                <EditLabel htmlFor={'className'}>class</EditLabel>
                <InputWrap>
                  <input type='text' name={'className'} placeholder="use space for separeted class name" className={'inputItems'} {...register('className')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'name'}>Name</EditLabel>
                <InputWrap>
                  <input type='text' name={'name'} className={'inputItems'} {...register('name')}/>
                </InputWrap>
            </FormGroup>
           <FormGroup>
                <EditLabel htmlFor={'value'}>value</EditLabel>
                <InputWrap>
                  <input type='text' name={'value'}  className={'inputItems'} {...register('value')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'min'}>min</EditLabel>
                  <InputWrap>
                  <input type='number' name={'min'} className={'inputItems'} style={{width:'40%'}} {...register('min')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'max'}>max</EditLabel>
                  <InputWrap>
                  <input type='number' name={'max'}  placeholder='Value' className={'inputItems'} style={{width:'40%'}} {...register('max')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'step'}>step</EditLabel>
                  <InputWrap>
                  <input type='number' name={'step'} className={'inputItems'} style={{width:'40%'}} {...register('step')}/>
                </InputWrap>
            </FormGroup>
            </form>
        </FormElemnts>
    )
}

export default EditFormValue
