import React from 'react';
import styled from 'styled-components';
import { Draggable } from 'react-beautiful-dnd';
import Header from './FormTypes/Header';
import Autocomplete from './FormTypes/Autocomplete';
import Button from './FormTypes/Button';
import Checkbox from './FormTypes/Checkbox';
import Datefield from './FormTypes/Datefield';
import File from './FormTypes/File';
import Paragraph from './FormTypes/Paragraph';
import Radio from './FormTypes/Radio';
import SelectT from './FormTypes/Select';
import Numbers from './FormTypes/Number';
import Textfield from './FormTypes/Textfield';
import Textarea from './FormTypes/Textarea';
const Container = styled.div`
  border: 1px solid lightgrey;
  border-radius: 2px;
  padding: 8px;
  margin-bottom: 8px;
  background-color: ${props => (props.isDragging ? 'lightgreen' : 'white')};
`;

const renderFields = (type,name,index) => {
            switch (type.type) {
             case "autocomplete": 
              return <Autocomplete key={index} type={type} name={name} index={index}/>
            case "button":
              return <Button key={index} type={type} name={name} index={index}/>
            case "checkbox-group":
              return <Checkbox key={index} type={type} name={name} index={index}/>
            case "date":
              return <Datefield key={index} type={type} name={name} index={index}/>
            case "file":
              return <File key={index} type={type} name={name} index={index}/>
            case "header":
            return <Header key={index} type={type} name={name} index={index}/>
            case "number":
              return <Numbers key={index} type={type} name={name} index={index}/>
            case "paragraph":
            return <Paragraph key={index} type={type} name={name} index={index} />
            case "radio-group":
              return <Radio key={index} type={type} name={name} index={index} />
            case "select":
            return <SelectT key={index} type={type} name={name} index={index}/>
            case "text":
            return <Textfield  key={index} type={type} name={name} index={index}/>
            case "textarea":
            return <Textarea key={index} type={type} name={name} index={index}/>
            default:
                return (
                    <div key={name}>
                        <span className="red-text">Invalid Field</span>
                    </div>
                )
            }
        }
const MainFromTask =(props)=> {
    return (
      <Draggable draggableId={props.id} index={props.index} >
        {(provided, snapshot) => {
        return(
                <Container
                {...provided.draggableProps}
                {...provided.dragHandleProps}
                ref={provided.innerRef}
                isDragging={snapshot.isDragging}
              >
                {/* <Header type={props.type} name={props.id} handleRemoveClick={props.handleRemoveClick} index={props.index} onChangeHandeler={props.onChangeHandeler}/> */}
                {renderFields(props.type,props.id,props.index)} 
              </Container>)

        }
        }

      </Draggable>
    );
  }
export default MainFromTask