import React from 'react';
import styled from 'styled-components';
import { Draggable } from 'react-beautiful-dnd';

const Container = styled.div`
  border: 1px solid lightgrey;
  border-radius: 2px;
  padding: 8px;
  margin-bottom: 8px;
  background-color: ${props => (props.isDragging ? 'lightgreen' : 'white')};
`;
const Clone = styled(Container)`
  + div {
    display: none!important;
  }`
export default class FromTypes extends React.Component {
  render() {
    return (
      <Draggable draggableId={this.props.type.type} index={this.props.index}>
        {(provided, snapshot) => (
          <>
          <Container
            {...provided.draggableProps}
            {...provided.dragHandleProps}
            ref={provided.innerRef}
            isDragging={snapshot.isDragging}
          >
            {this.props.type.label}
          </Container>
          {snapshot.isDragging && (<Clone>{this.props.type.label}</Clone>)}
          </>
          )}
      </Draggable>
    );
  }
}
