import React,{useEffect} from 'react'
import { useForm,useFieldArray } from "react-hook-form";
import {FormElemnts,FormGroup,InputWrap,EditLabel,} from '../../Styles'
import AddSelectOption from '../AddSelectOption';

function EditFormValue({selectData,name,index,data,setData,onChangeHandeler,setSelectData}) {
    const {register,getValues, setValue,control} = useForm({mode: 'onChange',  defaultValues:data})
     const { fields } = useFieldArray({
    control, // control props comes from useForm (optional: if you are using FormContext)
    name: "values", // unique name for your Field Array
    // keyName: "id", default to "id", you can change the key name
  })
    // useEffect(()=>{
    //    let value = watch()
    //    console.log(value)
    //    if(value.subtype){
    //        let nV = {...value,subtype:value.subtype.value}
    //        setData(nV)
    //    }else{
    //        setData(value)
    //    }
    // },[])

    const handleChange=(data)=>{
        let value = getValues()
        console.log(value)
        setValue('subtype',data.value)
        let nV = {...value,subtype:data.value}
        console.log(nV)
        setData(nV)
        onChangeHandeler(nV,index)
    }
    const onChange=()=>{
       let value = getValues()
      //  let arr = getValues('values')
      //   console.log(getValues('values'))
      // let garray = Object.entries(arr).map(v=>{
      //   console.log(v)
      //   return v[1]
      // })
      // console.log(garray)
      // let values = {...value,values:[...arr]}
       onChangeHandeler(value,index)
      setData(data)
    }
    // useEffect(() => {
    //   let value = getValues()
    //   let data = {...value,values:selectData}
    //   console.log(data)
    //     onChangeHandeler(data,index)
    // }, [selectData])
        // console.log(getValues('values'))
    return (
        <FormElemnts>
          <form onChange={onChange}>
            <FormGroup>
                <EditLabel htmlFor={'required'}>Required</EditLabel>
                <InputWrap>
                  <input type='checkbox' name={'required'} {...register('required')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={name}>Label</EditLabel>
                <InputWrap>
                  <input type='text' name={name} className={'inputItems'} {...register('label', { required: true })}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'description'}>Help Text</EditLabel>
                <InputWrap>
                  <input type='text' name={'description'} className={'inputItems'} {...register('description')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'toggle'}>Toggle</EditLabel>
                  <InputWrap>
                  <input type='checkbox' name={'toggle'} {...register('toggle')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'inline'}>Inline</EditLabel>
                  <InputWrap>
                  <input type='checkbox' name={'inline'} {...register('inline')}/> Display checkbox inline
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'className'}>Class</EditLabel>
                <InputWrap>
                  <input type='text' name={'className'} placeholder="use space for separeted class name" className={'inputItems'} {...register('className')}/>
                </InputWrap>
            </FormGroup>
            
            <FormGroup>
                <EditLabel htmlFor={'name'}>Name</EditLabel>
                <InputWrap>
                  <input type='text' name={'name'} className={'inputItems'} {...register('name')}/>
                </InputWrap>
            </FormGroup>
             <FormGroup>
                <EditLabel htmlFor={'other'}>other</EditLabel>
                  <InputWrap>
                  <input type='checkbox' name={'other'} {...register('other')}/> Let users enter an unlisted option
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'values'}>Options</EditLabel>
                <InputWrap>
                  <AddSelectOption selectData={selectData} setValue={setValue} data={data} index={index} register={register} onChangeHandeler={onChangeHandeler} setSelectData={setSelectData} />
                </InputWrap>
            </FormGroup>
          </form>
        </FormElemnts>
    )
}

export default EditFormValue
