import React , {useEffect,useState} from 'react'
import { useForm, } from "react-hook-form";
import {FormElemnts,FormGroup,InputWrap,EditLabel} from '../../Styles'
import Select from 'react-select'
const customStyles = {
  container: (provided, state) => ({
    ...provided,
    width: '100%',
  }),
}

function EditFormValue({name,index,data,setData,onChangeHandeler}) {
    const {register,getValues, setValue} = useForm({mode: 'onChange',  defaultValues:data})
    const [startDate, setStartDate] = useState(new Date());
    useEffect(()=>{
       setValue('name',`date-${startDate.toLocaleString().split(',')[0]}`)
    },[])
    const handleChange=(data)=>{
        let value = getValues()
        setValue('style',data.value)
        setValue('className','btn-'+data.value+' btn')
        let nV = {...value,style:data.value,className:'btn-'+data.value+' btn'}
        console.log(nV)
        setData(nV)
        onChangeHandeler(nV,index)
    }
    const onChange=()=>{
       let value = getValues()
       console.log(value)
       if(value.subtype && value.subtype.value){
           let nV = {...value,subtype:value.subtype.value}
           setData(nV)
        onChangeHandeler(nV,index)

       }else{
           setData(value)
        onChangeHandeler(value,index)

       }
    }
    return (
        <FormElemnts>
        <form onChange={onChange}>
        <FormGroup>
                <EditLabel htmlFor={'required'}>Required</EditLabel>
                <InputWrap>
                  <input type='checkbox' name={'required'} {...register('required')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={name}>Label</EditLabel>
                <InputWrap>
                  <input type='text' name={name} className={'inputItems'} {...register('label', { required: true })}/>
                </InputWrap>
            </FormGroup>
             <FormGroup>
                <EditLabel htmlFor={'description'}>Help Text</EditLabel>
                <InputWrap>
                  <input type='text' name={'description'} className={'inputItems'} {...register('description')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'placeholder'}>placeholder</EditLabel>
                <InputWrap>
                  <input type='text' name={'placeholder'} className={'inputItems'} {...register('placeholder')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'className'}>class</EditLabel>
                <InputWrap>
                  <input type='text' name={'className'} placeholder="use space for separeted class name" className={'inputItems'} {...register('className')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'name'}>Name</EditLabel>
                <InputWrap>
                  <input type='text' name={'name'} className={'inputItems'} {...register('name')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'value'}>Value</EditLabel>
                <InputWrap>
                  <input type='text' name={'value'}  placeholder='Value' className={'inputItems'} {...register('value')}/>
                </InputWrap>
            </FormGroup>
            </form>
        </FormElemnts>
    )
}

export default EditFormValue
