import React from 'react'
import { useForm, } from "react-hook-form";
import {FormElemnts,FormGroup,InputWrap,EditLabel} from '../../Styles'
import Select from 'react-select'
const customStyles = {
  container: (provided, state) => ({
    ...provided,
    width: '100%',
  }),
}
function EditFormValue({selectData,name,index,data,setData,onChangeHandeler}) {
    const {register,getValues, setValue} = useForm({mode: 'onChange',  defaultValues:data})
    // useEffect(()=>{
    //    let value = watch()
    //    console.log(value)
    //    if(value.subtype){
    //        let nV = {...value,subtype:value.subtype.value}
    //        setData(nV)
    //    }else{
    //        setData(value)
    //    }
    // },[])
    const handleChange=(data)=>{
        let value = getValues()
        console.log(value)
        setValue('subtype',data.value)
        let nV = {...value,subtype:data.value}
        console.log(nV)
        setData(nV)
        onChangeHandeler(nV,index)
    }
    const onChange=()=>{
       let value = getValues()
       console.log(value)
       if(value.subtype && value.subtype.value){
           let nV = {...value,subtype:value.subtype.value}
           setData(nV)
        onChangeHandeler(nV,index)

       }else{
           setData(value)
        onChangeHandeler(value,index)

       }
    }
    return (
        <FormElemnts>
        <form onChange={onChange}>
            <FormGroup>
                <EditLabel htmlFor={name}>Label</EditLabel>
                <InputWrap>
                  <input type='text' name={name} className={'inputItems'} {...register('label', { required: true })}/>
                </InputWrap>
                    
            </FormGroup>
            <FormGroup>
              <EditLabel>Types</EditLabel>
              <InputWrap>
               {/* <Controller
                name="subtype"
                control={control}
                render={({ field }) => <Select 
                 {...field} 
                className="basic-single"
                classNamePrefix="select"
                defaultValue={{value:data.subtype,label:data.subtype.toUpperCase()}}
                isSearchable={false}
                name="color"
                options={selectData}
                styles={customStyles}
                />}
      /> */}
             <Select 
                className="basic-single"
                classNamePrefix="select"
                defaultValue={{value:data.subtype,label:data.subtype.toUpperCase()}}
                isSearchable={false}
                name="color"
                options={selectData}
                styles={customStyles}
                onChange={handleChange}
                />
              
                </InputWrap> 
              
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'className'}>class</EditLabel>
                <InputWrap>
                  <input type='text' name={'className'} className={'inputItems'} placeholder="use space for separeted class name" {...register('className')}/>
                </InputWrap>
            </FormGroup>
            </form>
        </FormElemnts>
    )
}

export default EditFormValue
