import React, { useEffect, useState,createContext } from 'react'
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { AiOutlineDelete } from "react-icons/ai";
import { useForm,useFieldArray, } from "react-hook-form";
import { AddOptionBtn, AddOptionBtnContainer, RemoveBtn, ShortableOption, ShortableOptionLi } from '../Styles';
const reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};
function AddSelectOption({selectData,setSelectData,data,onChangeHandeler,index,register,setValue}) {
    const ListContext = createContext(selectData);
    const [List,setList] = useState(null)
  //   const {register,getValues,watch ,control,setValue} = useForm({mode: 'onChange',})
  //    const { fields, append, prepend, remove, swap, move, insert } = useFieldArray({
  //   control, // control props comes from useForm (optional: if you are using FormContext)
  //   name: "arr", // unique name for your Field Array
  //   // keyName: "id", default to "id", you can change the key name
  // })
   console.log(selectData)
  //   const onChange=()=>{
  //      let value = getValues('arr')
  //       setSelectData([...value])
  //       let values = {...data,values:[...value]}
  //       onChangeHandeler(values,index)
  //   }
    // const [list,setList]= useState(null)
  useEffect(()=>{
      setList(selectData)
      
    },[])
    useEffect(()=>{
      setList(selectData)
      
    },[selectData])

    const onDragEnd =(result) =>{
    // dropped outside the list
    if (!result.destination) {
    return;
    }
    // let value = getValues('arr')
    const items = reorder(
    selectData,
    result.source.index,
    result.destination.index
    );
    let a = [...items]
    setList(items);
    setSelectData(items)
    setValue('values',items)
    let values = {...data,values:items}
    onChangeHandeler(values,index)
    console.log(a)
    console.log(List)
  }
    const handleRemoveClick = i => {   
             console.log(i)             
            const tempArray = [...selectData];
            tempArray.splice(i, 1)
            setSelectData([...tempArray])
            setValue('values',[...tempArray])

            let values = {...data,values:[...tempArray]}
                onChangeHandeler(values,index)
            if(!tempArray.length){
            }
        };
    const handleAddClick = (i) => {
            console.log(i)
            let str = `option${selectData.length+1}`
            let newData = {value:str,label:str.toLocaleUpperCase(),selected:true}
            if(data.type === 'radio-group') newData = {value:str,label:str.toLocaleUpperCase()}
            let List =[...selectData,newData]
            setSelectData([...List])
              let values = {...data,values:[...List]}
            onChangeHandeler(values,index)
            console.log(List)
    }
    return (
      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable droppableId="droppable">
          {(provided, snapshot) => (
              <>
              <ShortableOption {...provided.droppableProps}
              ref={provided.innerRef}
              >
                <ShortableOptionLi >
                    <h4 style={{marginRight: "28%",marginLeft:'3%'}}>Value</h4>
                    <h4 style={{marginRight: "20%"}}>Label</h4>
                    <h4 style={{marginRight: "7%"}}>Delete</h4>
                </ShortableOptionLi>
                  {selectData && selectData.map((value,i)=>{ 
                          console.log(`${value.value}_${i}`)
                  return(
                 <Draggable key={`${value.value}_${value.label}`} draggableId={`${value.value}_${value.label}`} index={i}>
                  {(provided, snapshot) => (
                    <ShortableOptionLi
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      {...provided.dragHandleProps}
                      key={`${value.value}_${value.label}`}
                    >
                    {data.type === 'select' && data.multiple ? 
                    <input type='checkbox' name={`${value.value}_${value.label}`}  {...register(`values.${i}.selected`)} style={{marginRight:'7%'}}/> 
                    :
                    <input type='radio' name={`${value.value}_${value.label}`}  {...register(`values.${i}.selected`)} style={{marginRight:'7%'}}/> 

                    }
                    {data.type === 'radio-group' && <input type='radio' name={`${value.value}_${value.label}`}  {...register(`values.${i}.selected`)} style={{marginRight:'7%'}}/>}
                    {data.type === 'checkbox-group' && <input type='checkbox' name={`${value.value}_${value.label}`}  {...register(`values.${i}.selected`)} style={{marginRight:'7%'}}/>}
                    <input type='text' name={`${value.value}_${value.label}`}  {...register(`values.${i}.value`)} defaultValue={value.value} style={{marginRight:'7%'}}/>
                    <input type='text' name={`${value.value}_${value.label}`}  {...register(`values.${i}.label`)} defaultValue={value.label} style={{marginRight:'7%'}}/>
                    {i !==0 &&<RemoveBtn onClick={()=>handleRemoveClick(i)}><AiOutlineDelete/></RemoveBtn>}
                    </ShortableOptionLi>
                  )}
                </Draggable>
                )})}

                
              {provided.placeholder}
              </ShortableOption>
              <AddOptionBtnContainer>
                <AddOptionBtn onClick={()=>handleAddClick(selectData.length)} >Add Option +</AddOptionBtn>
            </AddOptionBtnContainer>
             {/* <div
              {...provided.droppableProps}
              ref={provided.innerRef}
              
            >
            {selectData && selectData.map((value,i)=>{ 
                  return(
                <Draggable key={`${value.value}_${value.label}`} draggableId={`${value.value}_${value.label}`} index={i}>
                  {(provided, snapshot) => (
                    <div
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      {...provided.dragHandleProps}
                    >
                    <li>Line {`${value.value}_${value.label}`} <input type='text' name={'vaule'+i}  {...register(`values.${i}.value`)} defaultValue={value.value} style={{marginRight:'7%'}}/>
                    <input type='text' name={'label'+i}  {...register(`values.${i}.label`)} defaultValue={value.label} style={{marginRight:'7%'}}/>
                    {i !==0 &&<RemoveBtn onClick={()=>handleRemoveClick(i)}><AiOutlineDelete/></RemoveBtn>}</li>
                    </div>
                  )}
                </Draggable>
              )})}*/}
              {provided.placeholder}
              </> 
            )}
            
              </Droppable>
      </DragDropContext>
    )
}

export default AddSelectOption
