import styled from 'styled-components';
export const FieldActions = styled.div`
    position: absolute;
    top: 0;
    right: 0;
    opacity: 0;
    &:hover{
    opacity: 1;
    }
`
export const TypesContainer = styled.div`
    position: relative;
    padding: 6px;
    clear: both;
    margin-left: 0;
    margin-bottom: 3px;
    background-color: #fff;
    transition: background-color 250ms ease-in-out, margin-top 400ms;
    &:hover ${FieldActions} {
    opacity: 1;
  }
    &:hover{
    border-color: #66afe9;
    outline: 0;
    box-shadow: inset 0 1px 1px rgb(0 0 0 / 10%), 0 0 8px rgb(102 175 233 / 60%);
    }
    &:first-child {
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
}
`
export const Label = styled.label`
    color: #666;
    margin-bottom: 5px;
    line-height: 27px;
    font-size: 16px;
    font-weight: normal;
    display: inline-block;
    max-width: 100%;
`
export const ActionBtn= styled.button`
    display: inline-block;
    width: 32px;
    height: 32px;
    padding: 0 6px;
    border-radius: 0;
    border-color: #c5c5c5;
    background-color: #fff;
    color: #c5c5c5;
    line-height: 32px;
    font-size: 16px;
    border-width: 0 0 1px 1px;
    cursor:pointer;
    &:first-child {
        border-bottom-left-radius: 5px;
    }        
    &:hover ${TypesContainer} {
        background-color:${props=>props.del ?'red' : '#fff'}
    }
    &:hover{
        background-color: ${props=> props.hoverBg};
        color: #fff;
    }
`
export const FormElemnts = styled.div`
    padding: 10px 10px;
    background: #f7f7f7;
    border-radius: 3px;
    margin: 0;
    border: 1px solid #c5c5c5;`
export const FormGroup = styled.div`
    width: 100%;
    clear: left;
    display:flex;
    margin-left: 0px;
    margin-bottom: 15px;
    box-sizing: border-box;
   
    `
    export const EditLabel = styled.label`
        width:16%;
        text-align:right;
        margin-right:3%;
        padding-top: 7px;
        margin-bottom: 0;
        font-weight: 700;
        white-space: nowrap;
        text-transform: capitalize;
    `
export const InputWrap= styled.div`
    display: block;
    padding: 6px 12px;
    line-height: 1.42857143;
    border-radius: 4px;
    width: 100%;
    font-size: 14px;
    border-radius: 4px;`

export const ShortableOption= styled.ol`
    display: block;
    padding: 6px 12px;
    line-height: 1.42857143;
    border-radius: 4px;
    width: 100%;
    font-size: 14px;
    border: 1px solid #c5c5c5;
`

    export const ShortableOptionLi= styled.li`
    display: flex;
    min-height:28px;
    cursor: move;
    margin: 1px;
    padding-right: 28px;
    clear: both;
    margin: 15px;
    margin-bottom: 3px;
    background-color: #fff;
    transition: background-color 250ms ease-in-out, margin-top 400ms;
`

    export const RemoveBtn= styled.div`
    opacity: 1;
    right: 8px;
    height: 18px;
    width: 18px;
    margin-left:8px;
    top: 14px;
    color: red;
    display: inline-block;
    padding: 6px 12px;
    margin-bottom: 0;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.42857143;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    user-select: none;
    border-radius: 4px;
    &:hover{
    background-color: red;
    text-decoration: none;
    color: #fff;
    }
    &:hover${ShortableOptionLi}{
    background-color: #fdd;
    }
    &:before{
        font-style: normal;
        font-weight: normal;
        speak: never;
        display: inline-block;
        text-decoration: inherit;
        width: 1em;
        margin-right: .2em;
        text-align: center;
        font-variant: normal;
        text-transform: none;
        line-height: 1em;
    }`
    export const AddOptionBtnContainer= styled.div`
    text-align: right;
    margin-top: 10px;
    width: 100%;
    margin-left: 2%;
    `
   export const AddOptionBtn= styled.div`
    background: #fff;
    padding: 5px 10px;
    border: 1px solid #c5c5c5;
    font-size: 14px;
    border-radius: 5px;
    float:right;
    cursor: default;`