import React from 'react'
import { useForm, } from "react-hook-form";
import {FormElemnts,FormGroup,InputWrap,EditLabel} from '../../Styles'
import Select from 'react-select'
const customStyles = {
  container: (provided, state) => ({
    ...provided,
    width: '100%',
  }),
}
const btns =['default','danger','warning','info','primary','success']
const buttons = btns.map(v=>{
  return {
    value:v,
    label:v.toUpperCase()
  }
})
function EditFormValue({name,index,data,setData,onChangeHandeler}) {
    const {register,getValues, setValue} = useForm({mode: 'onChange',  defaultValues:data})
    // useEffect(()=>{
    //    let value = watch()
    //    console.log(value)
    //    if(value.subtype){
    //        let nV = {...value,subtype:value.subtype.value}
    //        setData(nV)
    //    }else{
    //        setData(value)
    //    }
    // },[])
    const handleChange=(data)=>{
        let value = getValues()
        setValue('style',data.value)
        setValue('className','btn-'+data.value+' btn')
        let nV = {...value,style:data.value,className:'btn-'+data.value+' btn'}
        console.log(nV)
        setData(nV)
        onChangeHandeler(nV,index)
    }
    const onChange=()=>{
       let value = getValues()
       console.log(value)
       if(value.subtype && value.subtype.value){
           let nV = {...value,subtype:value.subtype.value}
           setData(nV)
        onChangeHandeler(nV,index)

       }else{
           setData(value)
        onChangeHandeler(value,index)

       }
    }
    return (
        <FormElemnts>
        <form onChange={onChange}>
            <FormGroup>
                <EditLabel htmlFor={name}>Label</EditLabel>
                <InputWrap>
                  <input type='text' name={name} className={'inputItems'} {...register('label', { required: true })}/>
                </InputWrap>
                    
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'style'}>Style</EditLabel>
                <InputWrap>
                  <Select 
                className="basic-single"
                classNamePrefix="select"
                defaultValue={buttons[0]}
                isSearchable={false}
                name="color"
                options={buttons}
                styles={customStyles}
                onChange={handleChange}
                />
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'className'}>class</EditLabel>
                <InputWrap>
                  <input type='text' placeholder="use space for separeted class name" name={'className'} className={'inputItems'} {...register('className')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'name'}>Name</EditLabel>
                <InputWrap>
                  <input type='text' name={'name'} className={'inputItems'} {...register('name')}/>
                </InputWrap>
            </FormGroup>
            <FormGroup>
                <EditLabel htmlFor={'value'}>Value</EditLabel>
                <InputWrap>
                  <input type='text'  placeholder='Value' name={'value'} className={'inputItems'} {...register('value')}/>
                </InputWrap>
            </FormGroup>
            </form>
        </FormElemnts>
    )
}

export default EditFormValue
