import React from 'react';
import styled from 'styled-components';
import { Droppable } from 'react-beautiful-dnd';
import FromTypes from './FormTypes';

const Container = styled.div`
  margin: 8px;
  border: 1px solid lightgrey;
  border-radius: 2px;
  width: 220px;
  width: 20%;
  display: flex;
  flex-direction: column;
`;
const Title = styled.h3`
  padding: 8px;
`;
const FormList = styled.div`
  padding: 8px;
  transition: background-color 0.2s ease;
  background-color: ${props => (props.isDraggingOver ? 'skyblue' : 'white')};
  flex-grow: 1;
  min-height: 100px;
`;

export default class TypeColumn extends React.Component {
  render() {
    return (
      <Container>
        <Title>{this.props.column.title}</Title>
        <Droppable droppableId={this.props.column.id} isDropDisabled={true}>
          {(provided, snapshot) => {
            return(
            <FormList
              ref={provided.innerRef}
              {...provided.droppableProps}
              isDraggingOver={snapshot.isDraggingOver}
            >
              {this.props.types.map((type, index) => <FromTypes key={type.type} type={type} index={index} />)}
              { provided.placeholder}
            </FormList>
          )
          }}
        </Droppable>
      </Container>
    );
  }
}
